const MYDATA =(props)=>{


    return(
        <div className="">
            <div className="">
          <div className="d-flex my-3 text-light">
            <div className="d-flex content bg-secondary">
            <div className="bg-dark box">
            {props.name}
            <span> <i class="fa fa-paperclip" aria-hidden="true"></i></span>
            </div>
            <p><Link><i class="fa fa-telegram" aria-hidden="true"></i></Link></p>
            </div>
            <div className="text-dark">{props.detail}</div>
            </div>
           </div>
        </div>
    )
}

const Command = () =>{



//this is styles for this component
const boxing ={
    display: "flex"
}



//the coeds above this line are styles for this component
    return (
        <div className="command" id="testimonies">
          <h1 className="text-center py-4">Some useful commands.</h1>
          <div className="d-flex justify-content-center">
            <p className="w-50 text-center">"Tori has a collection of simple 
            and useful commands that can help streamline your workflow and perform tasks more efficiently."</p>
          </div>
           {Data.map(item=><MYDATA name={item.name} detail={item.desc}/>)}
                
        </div>
    )
}

export default Command;