import { Link } from "react-scroll"
import logo from "../Asserts/logo.819e5eb3.png"
const Navbar = ()=>{

      const Links ={
        textDecoration: "none",
        color: "white",
        cursor: "pointer"
      }

    return(
        <nav
        style={{
            display: "flex",
            justifyContent: "space-between",
            width: "100%",
            height: "80px",
            backgroundColor: "#571845",
            padding: "0 30px",
            position: "fixed",
            zIndex: "1000"
        }}
        
        >
            <div style={{display: "flex"}}>
            <Link><img src={logo} alt="logo" width={25} 
             style={{marginTop: "25px",marginRight: "9px"}}/>
              </Link>
            <Link style={{lineHeight: "80px",textDecoration: "none",color: "white"}}><h1>TORI</h1></Link>
            </div>
            <ul
            style={{
                display: "flex",
                gap: "40px"
            }}
            >
                <li style={{listStyle: "none"}}><Link style={Links}
                 activeClass="active"
                 to="hova.ai"
                 spy={true}
                 smooth={true}
                 offset={-70}
                 duration={500}
                >Home</Link></li>
                <li style={{listStyle: "none"}}><Link style={Links}
                 activeClass="active"
                 to="pricing"
                 spy={true}
                 smooth={true}
                 offset={-70}
                 duration={500}
                >Pricing</Link></li>
                <li style={{listStyle: "none"}}><Link style={Links}
                 activeClass="active"
                 to="testimonies"
                 spy={true}
                 smooth={true}
                 offset={-70}
                 duration={500}
                >Testmonies</Link></li>
            </ul>
        </nav>
    )
}

export default Navbar;