import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"
import video  from "../Asserts/video.mp4"
import "./CSS/home.scss"
const Home = ()=>{



    const text = {
        fontSize: "16px",
        opacity: ".5",
        fontFamily: "sans-serif",
        wordSpacing: "5px",
        lineHeight: "30px"
    }
    const media = {
        opacity: '.8'
    }
    
    const chat = {
        backgroundColor: "#3b82f6",
        padding: "10px",
    }

    const whatsapp = {
        backgroundColor : "#157f3d",
        padding: "10px",
    }

    const telegram= {
        backgroundColor: "#3b82f6",
        padding: "10px",
    }

    const Links= {
        textDecoration: "none",
    }
    return (
        <div className="HOME container-fluid w-100 m-0" id='hova.ai' >
         <div className="container-fluid">
           <div className='main-content'>
            <h1 style={{color: "white",fontSize: "48px",
            wordSpacing: "7px",
            fontWeight: "300",
            paddingBottom: "50px"
            }}
            >Tori by <Link style={{textDecoration: "none"}} className='text-light'>  hova.ai</Link></h1>
            <div style={text} className='text-light'>
            Welcome to Tori, the ChatGPT powered bot running on
            WhatsApp at <Link style={media} className='text-light'>+250 737 829 991
            </Link> and on <Link style={media} className='text-light'>Telegram.</Link> We 
            can help you with a wide range of queries and concerns, and 
            I'm always available to chat with you. I 
            support voice notes, and can respond with speech! So
             go ahead and text me(or send a voice note😉)! I'm 
             ready and waiting to assist you.
            </div>
            <div className="contact-path">
             <div style={chat}>
                <Link style={Links} className='text-light text-center'><i class="fas fa-globe"></i> <span>Try ToriChat</span></Link>
                </div>
            <div style={whatsapp}>
                <Link style={Links}  className='text-light text-center'><i class="fa-brands fa-whatsapp"></i> <span>Try on WhatsApp</span></Link>
                </div>
            <div style={telegram}>
                <Link style={Links}  className='text-light text-center'><i class="fa fa-telegram" aria-hidden="true"></i> <span>Try on telegram</span></Link>
                </div>
            </div>
            <div className="numbers">
                <div>
                  <h1>Prompts</h1>
                  <p style={{fontSize: "20px",fontWeight:"bold",paddingTop: "15px"}}>48,278 +</p>
                </div>
                <div>
                <h1>Users</h1>
                <p style={{fontSize: "18px",fontWeight:"bold",paddingTop: "15px"}}>3,230 +</p>
                </div>
            </div>
           </div>
           <div className="video-container">
                     <ReactPlayer url={video} controls={true}
                     loop={true}
                     playing={true} width={300} height={400}/>
           </div>
         </div>
        </div>
    )
}

export default Home;